body.dg-open {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.dg-container * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.dg-container [disabled] {
  cursor: not-allowed;
  opacity: .3;
}
.dg-backdrop {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5000;
}
.dg-container {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5000;
}
.dg-content-cont {
  width: 100%;
  font-family: inherit;
}
.dg-main-content {
  width: 98%;
  /*width: calc(98% - 30px);*/
  max-width: 400px;
  padding: 15px;
  border-radius: 5px;
  margin: 25px auto;
  background-color: #ffffff;
}
.dg-content {
  font-size: 16px;
  line-height: 1.3em;
}
.dg-title {
  margin: 0 0 10px 0;
  padding: 0;
  font-size: 18px;
}
.dg-content-body {
  border-bottom: 2px solid #E1E6EA;
  padding-bottom: 15px;
}
.dg-content-footer {
  position: relative;
  padding: 15px 0 0;
}
.dg-form {
  background-color: ghostwhite;
  padding: 10px;
  margin-bottom: -15px;
}
.dg-content-cont--floating {
  position: absolute;
  top: 35%;
  -webkit-transform: translateY(-70%);
          transform: translateY(-70%);
  margin-top: 0;
}
@media all and (max-height: 700px) {
.dg-content-cont--floating {
    position: relative;
    top: 10%;
    -webkit-transform: none;
            transform: none;
    margin-top: 0;
}
}
.dg-btn {
  display: inline-block;
  position: relative;
  min-width: 80px;
  padding: 6px 20px;
  border-radius: 4px;
  outline: 0;
  border: 2px solid transparent;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-size: 16px;
  font-weight: 700;
}
.dg-btn:focus,
.dg-btn:active,
.dg-btn:link {
  outline: none;
}
.dg-btn::-moz-focus-inner {
  border: 0;
}
.dg-btn--cancel {
  color: #fefefe;
  background-color: #0096D9;
}
.dg-btn--ok {
  color: #0096D9;
  background-color: #fefefe;
  border-color: #0096D9;
}
.dg-pull-right {
  float: right;
}
.dg-btn.dg-btn--loading .dg-btn-content {
  visibility: hidden;
}
.dg-clear:before {
  content: ' ';
  display: block;
  clear: both;
}
.dg-content-body--has-title .dg-content {
  font-size: 14px;
}
.dg-container--has-input .dg-main-content {
  max-width: 450px;
}
.dg-container--has-input .dg-content {
  margin-bottom: 15px;
}
.dg-container--has-input .dg-content-body {
  border-bottom: none;
}
.dg-container--has-input .dg-form {
  border: 1px solid #E1E6EA;
  border-bottom: none;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.dg-container--has-input .dg-content-footer {
  background-color: ghostwhite;
  border: 1px solid #E1E6EA;
  border-top: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  padding: 0 10px 10px;
}
.dg-container .dg-highlight-1 {
  color: #FF8C00;
  font-weight: bold;
  border-bottom: 1px solid #2ba5ff;
}
.dg-container .dg-highlight-2 {
  color: #2ba5ff;
  font-weight: bold;
  border-bottom: 1px solid #FF8C00;
}

